import React from "react";
import {NavLink} from "react-router-dom";
import {Layout, Menu, theme} from "antd";


const Navbar = () => {

    const {token: {colorBgContainer}} = theme.useToken();
    const {Sider} = Layout;

    return <Sider style={{background: colorBgContainer}} width={200}>
        <Menu
            mode="inline"
            defaultSelectedKeys={['1']}
            defaultOpenKeys={['sub1']}
            style={{height: '100%'}}
        >
            <Menu.Item><NavLink to='/profile'></NavLink></Menu.Item>
            <Menu.Item><NavLink to='/dialogs'></NavLink></Menu.Item>
            <Menu.Item><NavLink to='/users'></NavLink></Menu.Item>
            <Menu.Item><NavLink to='/news'></NavLink></Menu.Item>
            <Menu.Item><NavLink to='/music'></NavLink></Menu.Item>
            <Menu.Item><NavLink to='/settings'></NavLink></Menu.Item>
        </Menu>
    </Sider>
}

export default Navbar